}

.form-user-info__content-block > div:first-child {
  width: 35%;
}

.form-user-info__content-block > div:last-child {
  width: 65%;
}

.form-user-info .icon {
  position: absolute;
  top: 40px;
  right: 45px;
  margin-left: auto;
  cursor: pointer;
}

.form-user-info .icon:hover {
