}

.form-user-registration legend {
  display: table;
  width: 100%;
  padding: 0;
  margin: 0;
  margin-bottom: 35px;
  font-size: 24px;
  line-height: 0.8;
}

.form-user-registration--invisible {
  display: none;
}

.form-user-registration input[type='submit'] {
