.page {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  width: inherit;
  height: inherit;
  margin: auto;
  overflow: hidden;
  font-family: "Roboto", "Arial", sans-serif;
  color: #333;
  background-color: #fff;
}

.validation-rules-container__title {
  display: flex;
  flex-flow: row wrap;
  padding: 0;
  margin: 0;
  margin-bottom: 5px;
  font-size: 12px;
  font-weight: 700;
  line-height: 14px;
  color: #777;
}

.validation-rules-container__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.validation-rules-list {
  flex-flow: column wrap;
  margin-top: 5px;
  margin-bottom: 10px;
}

.validation-rules-list__item {
  position: relative;
  padding-left: 20px;
  margin-bottom: 5px;
  font-size: 12px;
  line-height: 14px;
  color: #777;
}

.validation-rules-list__item::before {
  position: absolute;
  top: 50%;
  left: 0;
  display: block;
  width: 14px;
  height: 14px;
  content: '';
  border: 1px solid #777;
  border-radius: 50%;
  transform: translateY(-50%);
}

.validation-rules-list__item::after {
  position: absolute;
  top: 50%;
  left: 0;
  display: block;
  content: '';
}

.validation-rules-list__item--valid::after {
  width: 10px;
  height: 9px;
  background: url("../img/assets/validation-rules-list/checkmark.svg") no-repeat;
  transform: translate(20%, -50%);
}

.validation-rules-list__item--invalid::after {
  width: 10px;
  height: 10px;
  background: url("../img/assets/validation-rules-list/cross.svg") no-repeat;
  transform: translate(20%, -50%);
}
